html, body {
  height: 100%;
  width: 100%;
}

#root {
  height: 100%;
  width: 100%;
}

a {
  cursor: none;
  text-decoration: none;
}

.titles {
  background-color: rgba(230, 230, 230, 0.7);
  border-left: 10px solid black;
  padding-left: 10px;
  font-size: 40px;
  border-bottom: 10px solid white;
  padding-top: 10px;
}

.content {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 0px;
  font-size: 20px;
  position: relative;
}

.content-container {
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 20px;
  right: 20px;
  color: white;
  padding: 5px 5px 5px;
}
