header {
    background-color: #242424;
    font-family: Arial;
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 40px;
    color: white;
    font-weight: 700;
}

#sidebar-pusher {
    content: "";
    height: 0;
    width: 100%;
}

#closebtn {
    text-align: center;
    z-index: 2;
    padding: 8px 12px 8px 12px !important;
    position: absolute;
    top: 0;
    right: 25px;
    margin-left: 50px;
}

#sidenav {
    height: 0;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    opacity: 0.9;
    white-space: nowrap;
}

#sidenav div {
    padding: 10px 10px 10px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
}

#sidenav div:hover {
    color: #ffffff;
    background-color: black;
}

#overlay {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
}